.c3dHover{
  width: 300px;
  height: 380px;
  margin-bottom: 55px;
  -webkit-transform: rotate(-30deg) skew(15deg);
          transform: rotate(-30deg) skew(15deg);
  position: relative;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.c3d-layer:nth-child(4)::after{
    content: "";
    width: 200%;
    height: 100%;
    background: rgba(255,255,255,0.1);
    position: absolute;
    bottom: -248px;
    -webkit-transform: skew(-15deg) rotate(190deg);
            transform: skew(-15deg) rotate(190deg);
    right: -56px;
}
.c3dHover:hover .c3d-layer:nth-child(4){
  -webkit-transform: translate(60px, -60px);
          transform: translate(60px, -60px);
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}
.c3dHover:hover .c3d-layer:nth-child(3) {
  -webkit-transform: translate(40px, -40px);
          transform: translate(40px, -40px);
  -webkit-transition: 0.2 ease;
  transition: 0.2 ease;
}
.c3dHover:hover .c3d-layer:nth-child(2) {
  -webkit-transform: translate(20px, -20px);
          transform: translate(20px, -20px);
  -webkit-transition: 0.2 ease;
  transition: 0.2 ease;
}
.c3d-layer:nth-child(3){
  opacity:0.7;
}
.c3d-layer:nth-child(2) {
  opacity:0.5;
}
.c3d-layer:nth-child(1) {
  background: rgba(0,0,0,0.5);
}

.c3d-layer{
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  background: url(../../static/media/mobile.5b173b7f.png) no-repeat #000;
  width: 300px;
  height: 515px;
  background-size:300px;  
  position: absolute;
  border-radius: 15px;
}

