input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    margin: 4.5px 0;
    direction: rtl;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0px 0px 1px rgba(13, 13, 13, 0);
    background: #ff6384;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0px 0px 1px rgba(13, 13, 13, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    height: 14px;
    width: 40px;
    border-radius: 10px;
    background: #ff6384;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -4.7px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #ff7d98;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0px 0px 1px rgba(13, 13, 13, 0);
    background: #ff6384;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0px 0px 1px rgba(13, 13, 13, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    height: 14px;
    width: 40px;
    border-radius: 10px;
    background: #ff6384;
